import { Component, OnInit } from '@angular/core';
import {Meta, Title} from '@angular/platform-browser';
import {HomeService} from '../../shared/services/home.service';
import {Home} from '../../shared/models/home.model';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.less']
})
export class HomeComponent implements OnInit {

  home = new Home('', '', '', '');

  constructor(public homeService: HomeService,
              public meta: Meta,
              public titleService: Title) { }

  ngOnInit() {
    this.homeService.getHome().subscribe((data: Home []) => {
      if (data) {
        this.home = data[0];
        this.titleService.setTitle(this.home.ceo_title);
        this.meta.addTags([
          {name: 'description', content: this.home.ceo_desc},
          {name: 'keywords', content: this.home.ceo_keys}]);
      }
    });
  }

}
